@import url(https://fonts.googleapis.com/css2?family=Patrick+Hand+SC&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Amatic+SC&family=Rochester&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Comic+Neue&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Amatic+SC&family=Rochester&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Caveat&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Caveat&family=Patrick+Hand&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  margin: 0;
}

.react-calendar__navigation .react-calendar__navigation__arrow {
  font-family: "Patrick Hand SC";
  background-color: rgba(98, 169, 207, 0.8);
  color: white;
  font-size: 24px;
  border: none;
  cursor: pointer;
}

.react-calendar__navigation .react-calendar__navigation__label {
  font-size: 32px;
  font-family: "Patrick Hand SC";
  background-color: rgba(98, 169, 207, 0.8);
  color: white;
  border: none;
}

.react-calendar__tile {
  height: 70px;
  font-family: "Patrick Hand SC";
  background-color: rgba(98, 169, 207, 0.8);
  color: rgb(241, 236, 236);
  border: none;
  cursor: pointer;
}

.react-calendar__month-view__weekdays__weekday {
  font-size: 24px;
  text-align: center;
  color: white;
  font-family: "Patrick Hand SC";
  background-color: rgba(98, 169, 207, 0.8);
}

.react-calendar__tile:hover {
  background: rgba(168, 164, 167, 0.904);
}

.react-calendar__tile,
.react-calendar__month-view__days__day {
  font-size: 24px;
}

.react-calendar__tile--active {
  background: #006edc !important;
  color: white;
}

.highlight {
  background-color: rgb(176, 101, 226);
}

#toolbar {
  background-color: white;
}

.text-editor {
  margin-top: 15px;
  padding-top: 15px;
}

.ql-editor {
  min-height: 200px;
  background-color: white;
  color: black;
}

.submit-button {
  padding-top: 15px;
}

/* Set default font-family */
#editor-container {
  font-family: "Rochester";
  font-size: 18px;
  height: 375px;
}

.background {
  background-color: #5de4d2;
  background-image: linear-gradient(
    315deg,
    #5de4d2 25%,
    #6cdcbf 52%,
    #49a7da 90%
  );
  max-height: 100%;
  overflow-y: scroll;
  min-height: 100vh;
}

.textEditor {
  margin-top: 10px;
}

.wrapper-class {
  padding: 10px;
}

.editor-class {
  background-color: rgba(211, 211, 211, 0.8);
  width: 101.5%;
  padding: 15px;
  border: 1px solid #ccc;
  font-size: 24px;
  font-family: Arial;
  border-radius: 5px;
  min-height: 200px;
}

.toolbar-class {
  border: 1px solid #ccc;
  color: black;
  border-radius: 5px;
  width: 100%;
}

.ql-snow .ql-picker.ql-size .ql-picker-label[data-value]::before,
.ql-snow .ql-picker.ql-size .ql-picker-item[data-value]::before {
  content: attr(data-value) !important;
}

/* Set droplist names - -item is the currently selected font, -label is the font's appearance in the droplist*/
.ql-snow .ql-picker.ql-font .ql-picker-label[data-value=""]::before,
.ql-snow .ql-picker.ql-font .ql-picker-item[data-value=""]::before {
  content: "Default";
}

.ql-snow .ql-picker.ql-font .ql-picker-label[data-value="patrick-hand"]::before,
.ql-snow .ql-picker.ql-font .ql-picker-item[data-value="patrick-hand"]::before {
  content: "Patrick Hand";
  font-family: "Patrick Hand";
}

.ql-snow .ql-picker.ql-font .ql-picker-label[data-value="amatic-sc"]::before,
.ql-snow .ql-picker.ql-font .ql-picker-item[data-value="amatic-sc"]::before {
  content: "Amatic SC";
  font-family: "Amatic SC";
}

.ql-snow .ql-picker.ql-font .ql-picker-label[data-value="caveat"]::before,
.ql-snow .ql-picker.ql-font .ql-picker-item[data-value="caveat"]::before {
  content: "Caveat";
  font-family: "Caveat";
}

.ql-snow .ql-picker.ql-font .ql-picker-label[data-value="rochester"]::before,
.ql-snow .ql-picker.ql-font .ql-picker-item[data-value="rochester"]::before {
  content: "Rochester";
  font-family: "Rochester";
}

/****************************************************
Set the font-family content used for the HTML content.
*****************************************************/
.ql-font-patrick-hand {
  font-family: "Patrick Hand";
}

.ql-font-caveat {
  font-family: "Caveat";
}

.ql-font-amatic-sc {
  font-family: "Amatic SC";
}

.ql-font-rochester {
  font-family: "Rochester";
}

